// dlab-bnr-inr
.dlab-bnr-inr {
	background: #f8f8f8;
	height: 490px;
	background-size: cover;
	background-position: center;
	position: relative;
	z-index: 2;
	overflow: hidden;
	width: 100%;

	@include respond('tab-port') {
		height: 400px;

		.car-search-box {
			.form-control {
				.btn {
					padding: 15px 10px;
				}
			}
		}
	}

	@include respond ('phone') {
		height: 320px;
	}

	.container {
		display: table;
		height: 100%;

	}

	.dlab-bnr-inr-entry {
		height: 400px;
		vertical-align: middle;
		display: table-cell;

		@include respond('tab-port') {
			height: 400px;
		}

		@include respond('phone-land') {
			text-align: center;

			.breadcrumb-row {
				display: inline-block;
			}
		}

		@include respond('phone') {
			height: 340px;
		}
	}

	.dlab-separator {
		height: 10px;
		width: 128px;
		background-color: #fff;
		border-radius: 20px;

		@include respond ('tab-port') {
			height: 7px;
		}
	}

	h1 {
		font-weight: 700;
		font-size: 60px;
		margin-bottom: 10px;
		text-transform: capitalize;
		line-height: 1.1;

		@include respond('tab-port') {
			font-size: 42px;
		}

		@include respond('phone-land') {
			font-size: 36px;
		}
	}

	.car-search-box {
		margin: 30px auto;
		max-width: 900px;
	}


	// height sm
	&.dlab-bnr-inr-xl {
		height: 620px;

		.dlab-bnr-inr-entry {
			height: 620px;
		}
	}

	&.dlab-bnr-inr-lg {
		height: 575px;

		.dlab-bnr-inr-entry {
			height: 575px;
		}
	}

	// height sm
	&.dlab-bnr-inr-sm {
		height: 300px;

		.dlab-bnr-inr-entry {
			height: 300px;
		}
	}

	&.bnr-no-img:before {
		content: none;
	}
}

.breadcrumb-row {
	ul {
		background: transparent;
		padding: 0;

		li {
			padding: 0;
			margin-right: 3px;
			display: inline-block;
			font-size: 15px;
			font-weight: 600;
			color: $white;

			&.active {
				color: $white;
			}

			a {
				color: $white;
			}
		}

		.breadcrumb-item+.breadcrumb-item::before {
			content: "|";
			color: $white;
			font-weight: 700;
			font-size: 15px;
			padding-right: 10px;
		}
	}
}

