.square-bx {
	position: absolute;
	width: 170px;
	height: 130px;
	top: 50%;
	transform: translate(-25%, -50%);
	right: 25%;
	z-index: -1;

	&:before,
	&:after {
		content: "";
		position: absolute;
		background-color: var(--primary);
		border-radius: 10px;
	}

	&:before {
		height: 95px;
		width: 95px;
		left: 0;
		top: 0;
	}

	&:after {
		height: 55px;
		width: 55px;
		right: 0;
		bottom: 0;
	}

	@include respond('tab-port') {
		width: 100px;
		height: 80px;
		top: 40%;
		transform: translate(-25%, -40%);

		&:before {
			height: 60px;
			width: 60px;
		}

		&:after {
			height: 30px;
			width: 30px;
		}
	}

	@include respond('phone') {
		display: none;
	}
}