.spinner .ball {
  width: 30px;
  height: 30px;
  background-color: var(--primary);
  border-radius: 50%;
  display: inline-block;
  animation: motion 3s cubic-bezier(0.77, 0, 0.175, 1) infinite;
}
.spinner p {
  margin-top: 5px;
  letter-spacing: 3px;
  font-size: 16px;
  font-weight: 600;
  color: var(--title);
}
.spinner p:after {
  content: "";
  animation: dots 1s infinite;
}

@keyframes dots {
  0% {
    content: ".";
  }
  50% {
    content: "..";
  }
  100% {
    content: "...";
  }
}
@keyframes motion {
  0% {
    transform: translateX(0) scale(1);
  }
  25% {
    transform: translateX(-50px) scale(0.3);
  }
  50% {
    transform: translateX(0) scale(1);
  }
  75% {
    transform: translateX(50px) scale(0.3);
  }
  100% {
    transform: translateX(0) scale(1);
  }
}
